.NavigationItem {
  margin: 10px 0;
  box-sizing: border-box;
  display: inline-block;
  width: 100%;
}

.NavigationItem a {
  color: black;
  text-decoration: none;
  width: 100%;
  box-sizing: border-box;
  display: block;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
  color: #50269e;
}

@media (min-width: 900px) {
  .NavigationItem {
    margin: 0;
    display: flex;
    height: 100%;
    width: auto;
    align-items: center;
  }

  .NavigationItem a {
    color: white;
    height: 100%;
    padding: 16px 10px;
    border-bottom: 4px solid transparent;
  }

  .NavigationItem a:hover,
  .NavigationItem a:active,
  .NavigationItem a.active {
    background-color: #e6e6e6;
    border-bottom: 4px solid #8f3bc4;
    color: black;
  }
}
